import { useNavigate } from "react-router-dom"
import { useForm } from "react-hook-form"

//YUP  usado para formulrios e suas validaes.
import { yupResolver } from "@hookform/resolvers/yup"
import * as yup from "yup"

import { MyRegister } from "./Styles/Register"
import { UserContext } from "../contexts/UserContext"
import { useContext } from "react"


function Register() {

  //Navigate para voltar do REGISTER para o LOGIN.
  const navigate = useNavigate()
  const {userRegister} = useContext(UserContext)

  //usando a API.
  /*const userRegister = (data) => {
    axios
      .post("https://kenziehub.herokuapp.com/users", data)
      .then((response) => {

      //configurando os alertas da pgina e navegando para o LOGIN novamente. 
      Swal.fire(
          "Cadastro feito com sucesso!",
          "Estamos quase l!",
          "Sucesso!"
        )
        navigate("/")})

      .catch(
        Swal.fire({
          icon: "error",
          title: "Xiii!",
          text: "Seu e-mail j foi usado anteriormente!"})
      )}*/

  //A senha precisa ter letras maisculas e minsculas, um nmero e um caracter especial, 
  //alm de ser de pelo menos 8 dgitos.
  const validation = yup.object().shape({
    email: yup.string().required("Este campo  obrigatrio!"),
    password: yup
      .string()
      .required("Campo obrigatrio")
      .matches(
        /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})/,
        "Sua senha  muito fraca. Se esfoce mais!"),
    bio: yup.string().required("Este campo  obrigatrio!"),
    contact: yup.string().required("Este campo  obrigatrio!"),
    course_module: yup.string().required("Este campo  obrigatrio!"),
    name: yup.string().required("Este campo  obrigatrio!")})


  //const que gera alerta de errors quando o form no cumpre os requisitos. puxa a funo Validation. 
  const {register, handleSubmit, formState: { errors }} = useForm({resolver: yupResolver(validation)})

  return (
    <MyRegister>
      <span className="SpanRegister">
        <h1 className="Logo">Kenzie Hub</h1>
        <button className="LoginBtn"
          onClick={() => {navigate("/")}}>Voltar</button>
      </span>
      <form className="MyForm" onSubmit={handleSubmit(userRegister)}>
        <h2 className="Tittle">Crie sua conta com a gente.  rpidinho, juro!</h2>
        <label className="Tittles">
          Nome
          <input
            type="text"
            className="Space"
            placeholder="Digite aqui seu nome"
            name="name"
            {...register("name")}
          />
          <p className="errorMessage">{errors.name?.message}</p>
        </label>
        <label className="Tittles">
          E-mail
          <input
            type="email"
            className="Space"
            placeholder="Digite aqui seu e-mail"
            name="email"
            {...register("email")}
          />
          <p className="errorMessage">{errors.email?.message}</p>
        </label>

        <label className="Tittles">
          Senha
          <input
            type="password"
            className="Space"
            placeholder="Digite aqui sua senha"
            name="password"
            {...register("password")}
          />
          <p className="errorMessage">{errors.password?.message}</p>
        </label>
        <label className="Tittles">
          Confimar Senha
          <input
            type="password"
            className="Space"
            placeholder="Confirme aqui sua senha"
          />
          <p className="errorMessage">{errors.password?.message}</p>
        </label>
        <label className="Tittles">
          Bio
          <input
            type=""
            className="Space"
            placeholder="Digite aqui sua biografia"
            name="bio"
            {...register("bio")}
          />
          <p className="errorMessage">{errors.bio?.message}</p>
        </label>
        <label className="Tittles">
          Contato
          <input
            type="text"
            className="Space"
            placeholder="Digite aqui seu contato"
            name="contact"
            {...register("contact")}
          />
          <p className="errorMessage">{errors.contact?.message}</p>
        </label>
        <label className="Tittles">
          Selecionar mdulo
          <select
            className="Space"
            placeholder="Mdulos"
            name="course_module"
            {...register("course_module")}
          >
            <option value="Primeiro-mdulo">
              Primeiro mdulo (Introduo ao Frontend)
            </option>
            <option value="Segundo-mdulo">
              Segundo mdulo (Frontend Avanado)
            </option>
            <option value="Terceiro-mdulo">
              Terceiro mdulo (Introduo ao Backend)
            </option>
            <option value="Quarto-mdulo">
              Quarto mdulo (Backend Avanado)
            </option>
          </select>
        </label>
        <button className="SubmitBtn">Cadastrar</button>
      </form>
    </MyRegister>
  )
}

export default Register